.header {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  left: 0;
  top: 0;
}

.contenido-header {
  display: none;
}
@media (min-width: 768px) {
  .contenido-header {
    align-items: center;
    justify-content: space-between;
    display: flex;
  }
}
.contenido-header .navegacion-principal {
  padding: 2rem 0;
  margin-right: 4.2rem;
}

.menu-fixed {
  background-color: #0466c8;
  box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.2);
}
.menu-fixed.fijo {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 4000;
}

.navegacion-enlace {
  margin: 0 0.8rem;
  font-size: 2rem;
  color: #fff;
  padding: 0.7rem;
  border-radius: 0.5rem;
}
.navegacion-enlace:hover {
  transition: 0.7s all;
  color: #e4e4ea;
}

.botton-up {
  font-size: 2.5rem;
  color: #fff;
  margin-left: 5rem;
}
.botton-up:hover {
  cursor: pointer;
  color: #e4e4ea;
  transition: 0.7s all;
}

#scrollBtn {
  transition: transform 0.2s ease-in-out;
}

#scrollBtn:active {
  transform: scale(0.95);
}

.navegacion-enlace.active {
  background-color: #0c396a;
}

@media (min-width: 768px) {
  .barra-responsive {
    display: none;
  }
}
.barra-responsive .menu-boton,
.barra-responsive .menu-close {
  top: 0;
  position: fixed;
  z-index: 1000;
  color: #fff;
  cursor: pointer;
  background-color: transparent;
  font-size: 2.5rem;
  padding: 0.5rem 1rem;
  margin: 1.5rem 1.5rem;
  border: 0.1rem solid #fff;
  border-radius: 0.5rem;
  background-color: rgba(76, 80, 84, 0.8);
}

.hidden {
  display: none;
}

.navegacion__responsive {
  position: fixed;
  background: #0466c8;
  z-index: 3;
  top: 0;
  left: -100vw;
  width: 75%;
  bottom: 0;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  transition: 0.5s;
  padding-top: 10rem;
}
.navegacion__responsive .navegacion__enlace-responsive {
  margin-left: 5rem;
  font-size: 3rem;
  color: #fff;
  text-decoration: none;
  line-height: 7rem;
}
.navegacion__responsive .navegacion__enlace-responsive:hover {
  cursor: pointer;
}

.navegacion__responsive.is-active {
  left: 0;
}

.navegacion__enlace-responsive.active {
  color: #0c396a;
  font-weight: bold;
}

.noscroll {
  overflow: hidden;
}

.hero {
  margin: 10rem 0;
}
.hero-contenido {
  display: grid;
}
@media (min-width: 768px) {
  .hero-contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    align-items: center;
  }
}
.hero-imagen {
  grid-row: 2/3;
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .hero-imagen {
    grid-row: inherit;
  }
}
.hero-texto {
  text-align: center;
  color: #fff;
}
.hero-texto h2 {
  color: #0466c8;
}
.hero-texto p {
  color: #fff;
}
.hero-texto h2,
.hero-texto p {
  font-size: 4rem;
}
@media (min-width: 768px) {
  .hero-texto h2,
  .hero-texto p {
    font-size: 5rem;
  }
}
@media (min-width: 998px) {
  .hero-texto h2,
  .hero-texto p {
    font-size: 7rem;
  }
}

.main-titulo {
  font-size: 4rem;
  margin: 17rem 0 5rem 0;
  color: #0466c8;
}
@media (min-width: 768px) {
  .main-titulo {
    font-size: 6rem;
  }
}

.sobre-mi {
  color: #fff;
  justify-items: center;
  align-items: center;
  margin-bottom: 10rem;
}
@media (min-width: 998px) {
  .sobre-mi {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0rem;
  }
}

.tecnologias {
  display: grid;
  grid-template-columns: repeat(2, 0fr);
  column-gap: 10rem;
  row-gap: 2rem;
  justify-content: center;
}
.tecnologias p {
  margin: 1rem 0;
  font-size: 2.1rem;
}
@media (min-width: 768px) {
  .tecnologias {
    column-gap: 10rem;
    grid-template-columns: repeat(3, 0fr);
  }
}

.contenedor-tecnologias {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contenedor-tecnologias i {
  height: 7.5rem;
  width: 7.5rem;
  border-radius: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.iconos {
  font-size: 5rem;
}
.iconos .react {
  color: #61dafb;
}
.iconos .html {
  color: #e34f26;
}
.iconos .css3 {
  color: #1572b6;
}
.iconos .javascript {
  color: #f7df1e;
}
.iconos .figma {
  color: #f24e1e;
}
.iconos .bootstrap {
  color: #563d7c;
}
.iconos .github {
  color: #f05032;
}
.iconos .sass {
  color: #cc6699;
}
.iconos .gulp {
  color: #da4648;
}

.experiencia-titulo {
  color: #0466c8;
  margin: 17rem 0 0 0;
  font-size: 5rem;
}

.experiencia-contenedor {
  color: #fff;
  margin-bottom: 8rem;
}
@media (min-width: 768px) {
  .experiencia-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}

.educacion {
  margin-top: 8rem;
}
.educacion-bloque {
  margin: 2rem 0;
  padding: 1.2rem;
  border-radius: 1rem;
}
.educacion-bloque h4 {
  float: left;
  margin: 0;
  font-size: 2.2rem;
}
.educacion-bloque span {
  color: #74808a;
  float: right;
}
.educacion-bloque p {
  clear: both;
  color: #74808a;
  margin: 0;
}
.educacion-enlace a {
  float: right;
  color: #dcd9d9;
  text-decoration: underline;
}

.experiencia {
  margin: auto;
}
.experiencia a {
  font-size: 2.5rem;
  color: #fff;
  text-decoration: underline;
  float: right;
  margin-right: 2rem;
}
.experiencia-bloque {
  padding: 1rem;
  border-bottom: 3px solid #dcd9d9;
  margin-bottom: 2rem;
}

.proyectos h2 {
  font-size: 5rem;
  color: #0466c8;
}
.proyectos-contenedor {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .proyectos-contenedor {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.proyectos-articulo {
  width: 70%;
  margin: 2rem auto;
  border-radius: 10px;
  overflow: hidden;
  background: #0466c8;
}
@media (min-width: 768px) {
  .proyectos-articulo {
    width: 100%;
  }
}
.proyectos-articulo picture {
  margin: 0;
}
.proyectos-articulo img {
  width: 100%;
  height: 17rem;
  object-fit: cover;
  object-position: top;
}
.proyectos-detail {
  background-color: #e4e4ea;
  position: relative;
  text-align: center;
  margin: -2em 0.5em 0.5em;
  padding: 1em;
}
.proyectos-detail h3 {
  font-size: 3.5rem;
  color: #0466c8;
}
.proyectos-detail a {
  color: #0466c8;
  border: 1px solid;
  text-decoration: none;
  border-radius: 5px;
  padding: 5px 8px;
}

.footer {
  background-color: #0466c8;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 17rem;
  color: #fff;
}
.footer-redes {
  font-family: "FontAwesome", sans-serif;
  display: flex;
  justify-content: center;
}
.footer-redes a {
  width: 5.3rem;
  height: 5.3rem;
  display: block;
  border-radius: 50%;
  text-decoration: none;
  background-color: rgb(232, 225, 225);
  overflow: hidden;
  text-align: center;
  font-size: 2.5rem;
  margin: 0 10px;
}
.footer-texto a {
  text-decoration: underline;
}

.footer-redes a::before {
  display: block;
  margin: 1.2rem 0;
  transition: all 0.5s ease-in-out;
}

.footer-redes a:hover::before {
  margin-top: -3.5rem;
}

.footer-redes a::after {
  display: block;
}

/* ///////////////////////////////////////////////////////////////// */
.footer-redes .one::before {
  content: "\f09b";
  color: #f05032;
}

.footer-redes .one::after {
  content: "\f09b";
  color: #f05032;
}

/* ///////////////////////////////////////////////////////////////// */
.footer-redes .two::before {
  content: "\f2b6";
  color: #0072C5;
}

.footer-redes .two::after {
  content: "\f2b6";
  color: #0072C5;
}

/* ///////////////////////////////////////////////////////////////// */
.footer-redes .three::before {
  content: "\f08c";
  color: #0A66C2;
}

.footer-redes .three::after {
  content: "\f08c";
  color: #0A66C2;
}